<template>
  <div class="hello">
    <!---->
    <div class="box1">
      <h1>
        <img src="../assets/cw.png" alt="" width="40px" />{{ msg }}
        <el-tag size="small"></el-tag>
      </h1>
      <div style="position: absolute; left: 35px; top: 65px">
        <el-button style="border-style: none">
          <span>{{ dateFormat(newDate) }}</span>
        </el-button>
      </div>
    </div>
    <br />

    <!---->
    <div class="box2">
      <el-carousel :interval="4000" type="card" height="220px">
        <el-carousel-item v-for="item in items" :key="item.id">
          <img :src="item.url" width="400px" />
        </el-carousel-item>
      </el-carousel>
    </div>

    <el-row>
      <el-col :span="4">
        <!---->
        <div style="height: 280px">
          <el-radio-group v-model="isCollapse" style="margin-bottom: 20px">
            <button
              @click="
                show = flase;
                card = true;
              "
              style="border-style: none; opacity: 0.9"
            >
              <el-radio-button :label="false"></el-radio-button>
            </button>
            <button
              @click="
                show = true;
                card = false;
              "
              style="border-style: none; opacity: 0.9"
            >
              <el-radio-button :label="true"></el-radio-button>
            </button>
            <!---->
            <el-popover placement="right" width="100" trigger="click">
              <h4></h4>
              <router-link :to="{ path: 'homepage/main' }">
                <el-button type="primary" icon="el-icon-loading" size="small">
                  
                </el-button>
              </router-link>
              <el-button
                style="position: relative; top: 5px; left: 24px"
                icon="el-icon-question"
                circle
                slot="reference"
              ></el-button>
            </el-popover>
          </el-radio-group>
          <el-menu
            default-active="1-4-1"
            class="el-menu-vertical-demo"
            @open="handleOpen"
            @close="handleClose"
            :collapse="isCollapse"
            router
          >
            <el-menu-item index="/homepage/main">
              <i class="el-icon-house"></i>
              <span slot="title"></span>
            </el-menu-item>
            <el-menu-item index="/homepage/knowledge">
              <i class="el-icon-chat-line-round"></i>
              <span slot="title"></span>
            </el-menu-item>
            <el-menu-item index="/homepage/process">
              <i class="el-icon-edit-outline"></i>
              <span slot="title"></span>
            </el-menu-item>
            <el-menu-item index="/homepage/relation">
              <i class="el-icon-postcard"></i>
              <span slot="title"></span>
            </el-menu-item>
          </el-menu>
          <div
            style="
              position: absolute;
              bottom: 5px;
              left: 80px;
              writing-mode: vertical-rl;
            "
          >
            <h2 v-show="show"></h2>
          </div>
        </div></el-col
      >

      <!---->
      <transition name="el-fade-in-linear"
        ><el-col :span="5" v-show="card">
          <div>
            <el-card shadow="hover">
              <h4></h4>
              <h4></h4>
              <h4></h4>
              <h4></h4>
            </el-card>
          </div>
        </el-col></transition
      >

      <!---->
      <transition name="el-fade-in-linear">
        <el-col :span="10" :offset="1">
          <div>
            <el-card shadow="hover">
              <el-descriptions title="" style="height: 230px">
                <el-descriptions-item label=""
                  ></el-descriptions-item
                >
                <el-descriptions-item label=""
                  >10000000000</el-descriptions-item
                >
                <el-descriptions-item label=""
                  ></el-descriptions-item
                >
                <el-descriptions-item label="">
                  
                </el-descriptions-item>
                <el-descriptions-item label=""
                  ><a href="http://scadr.xyz/"
                    >http://scadr.xyz/</a
                  ></el-descriptions-item
                >
                <el-descriptions-item label=""
                  ></el-descriptions-item
                >
                <el-descriptions-item label=""
                  >250</el-descriptions-item
                >
              </el-descriptions>
            </el-card>
          </div>
        </el-col>
      </transition>

      <!---->
      <transition name="el-fade-in-linear">
        <el-col :span="3" :offset="1" v-show="card">
          <div>
            <el-card shadow="hover" :body-style="{ padding: '0px' }">
              <img src="../assets/link.png" alt="" width="100%" />
              <div style="text-align: center; margin-bottom: 20px">
                <span></span>
              </div>
            </el-card>
          </div>
        </el-col>
      </transition>

      <!---->
      <transition name="el-fade-in-linear">
        <el-col :span="6" :offset="1" v-show="show">
          <div>
            <h4></h4>
            <p></p>
            <p></p>
            <p></p>
          </div>
          <div>
            <h4></h4>
            <el-button type="text" @click="dialogVisible = true"
              ><i class="el-icon-s-promotion"></i></el-button
            >
            <el-dialog
              title=""
              :visible.sync="dialogVisible"
              width="30%"
              :before-close="handleClose"
            >
              <span>30</span>
              <span>{{ 30 * num }}</span>
              <el-input-number
                v-model="num"
                @change="handleChange"
                :min="1"
                :max="10000"
                label=""
              ></el-input-number>
              <span>()</span>
              <el-dialog
                width="30%"
                title=""
                :visible.sync="innerVisible"
                append-to-body
                ><div>
                  <img src="../assets/love-1.gif" alt="" />
                  <img src="../assets/love-1.gif" alt="" />
                  <img src="../assets/love-1.gif" alt="" />
                  <img src="../assets/love-1.gif" alt="" />
                  <span></span>
                </div>
              </el-dialog>
              <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false"> </el-button>
                <el-button type="primary" @click="innerVisible = true"
                  > </el-button
                >
              </span>
            </el-dialog>
          </div>
        </el-col>
      </transition>
    </el-row>

    <!---->
    <footer>
      <el-divider></el-divider>
      <p>
        
        <el-button type="text" @click="drawer = true" style="margin-left: 5px"
          ><b></b></el-button
        >
      </p>
    </footer>

    <!--  -->
    <el-drawer title="" :visible.sync="drawer" :with-header="false">
      <div>
        <br /><br />
        <p class="edp">
          
          
        </p>
        <br />
        <p>
          <router-link to="homepage/relation"></router-link
          >
        </p>
        <br />
        <p>1000000000</p>
      </div>
    </el-drawer>
  </div>
</template>

<script>
export default {
  name: 'Index',
  data () {
    return {
      msg: '',
      isCollapse: true,
      drawer: false,
      show: true,
      card: false,
      dialogVisible: false,
      innerVisible: false,
      num: 1,
      items: [
        { id: 1, url: require("../assets/cat-1.jpg") },
        { id: 2, url: require("../assets/cat-2.jpg") },
        { id: 3, url: require("../assets/cat-3.jpg") },
        { id: 4, url: require("../assets/snowflakes-5.jpg") },
        { id: 5, url: require("../assets/kitty-5.jpg") },
        { id: 6, url: require("../assets/husky-6.jpg") },
      ],
      newDate: new Date(),
    }
  },
  methods: {
    homepage () {
      this.$router.push('/homepage')
    },
    handleOpen (key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose (key, keyPath) {
      console.log(key, keyPath);
    },
    dateFormat () {
      var date = new Date()
      var year = date.getFullYear()
      var month = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1
      var day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate()
      var hours = date.getHours() < 10 ? '0' + date.getHours() : date.getHours()
      var minutes = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()
      var seconds = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds()
      let week = date.getDay() // 
      let weekArr = ['', '', '', '', '', '', '']
      //  
      return year + '' + month + '' + day + ' ' + hours + ':' + minutes + ':' + seconds + ' ' + weekArr[week]
    },
    handleClose (done) {
      this.$confirm('')
        .then(_ => {
          done();
        })
        .catch(_ => { });
    },
    handleChange (value) {
      console.log(value);
    },
  },
  beforeCreate () {
  },
  // 
  mounted () {
    let that = this
    this.timer = setInterval(function () {
      that.newDate = new Date().toLocaleString()
    })
  },
  // 
  beforeDestroy: function () {
    if (this.timer) {
      clearInterval(this.timer)
    }
  },
}
</script>

<!--  -->
<style scoped>
.hello {
  display: inline;
  width: 100%;
  height: 100%;
}

.box1 {
  text-align: left;
}
.box1 img {
  position: relative;
  top: 7px;
}

.box2 {
  text-align: center;
}

h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
/*  */
footer {
  position: relative;
  bottom: 0;
  font-family: "Courier New", Courier, monospace;
}
.edp {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 1rem;
  text-indent: 2em;
  line-height: 20px;
}
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}

p {
  text-indent: 2em;
}

.demo-image__lazy .el-image {
  display: block;
  min-height: 200px;
  margin-bottom: 10px;
}

body {
  margin: 0;
  padding: 0;
}
</style>



