<script>
export default {
  name: 'Main',
  data () {
    return {
      items: [
        { id: 1, url: require("../assets/cat-8.jpg") },
        { id: 2, url: require("../assets/friends-4.jpg") },
        { id: 3, url: require("../assets/dog-190056.jpg") },
        { id: 4, url: require("../assets/husky-6.jpg") },
      ],
      value: new Date()
    }
  }
}
