<style scoped>
a {
  display: block;
  text-decoration: none;
  white-space: nowrap;
}

a:hover {
  text-decoration: none;
}

#footer {
  height: 40px;
  line-height: 40px;
  position: fixed;
  bottom: 0;
  width: 100%;
  text-align: center;
  font-family: Arial;
  font-size: 12px;
  letter-spacing: 1px;
}

.content {
  height: 1800px;
  width: 100%;
  text-align: center;
}

h2 {
  color: #e6a23c;
}
</style>


