<template>
  <div>
    <el-row>
      <el-col :span="4">
        <div class="box">
          Process
          <h2></h2>
          <p>{{ fo.name }}</p>
          <p>{{ fo.js }}</p>
          <p>UID{{ fo.uid }}</p>
          <p>{{ fo.sex }}</p>
          <p>{{ fo.bir }}</p>
          <el-button-group
            ><el-button type="primary"></el-button>
            <el-button @click="dialogVisible = true"></el-button>
          </el-button-group>
          <el-dialog
            title=""
            :visible.sync="dialogVisible"
            width="30%"
            :before-close="handleClose"
            ><div>
              <el-input v-model="fo.name"></el-input>
              <el-input v-model="fo.js"></el-input>
              <el-input v-model="fo.uid"></el-input>
              <el-input v-model="fo.sex"></el-input>
              <el-input v-model="fo.bir"></el-input>
            </div>
            <span slot="footer" class="dialog-footer">
              <el-button @click="dialogVisible = false"> </el-button>
              <el-button type="primary" @click="dialogVisible = false"
                > </el-button
              >
            </span>
          </el-dialog>
        </div>
      </el-col>
      <el-col :span="18">
        <div class="box2">
          <h2></h2>
          <el-form ref="form" :model="form" label-width="80px">
            <el-form-item label="">
              <el-input v-model="form.name"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="form.region" placeholder="">
                <el-option label="" value="shanghai"></el-option>
                <el-option label="" value="beijing"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-col :span="11">
                <el-date-picker
                  type="date"
                  placeholder=""
                  v-model="form.date1"
                  style="width: 100%"
                ></el-date-picker>
              </el-col>
              <el-col class="line" :span="2">-</el-col>
              <el-col :span="11">
                <el-time-picker
                  placeholder=""
                  v-model="form.date2"
                  style="width: 100%"
                ></el-time-picker>
              </el-col>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="form.delivery"></el-switch>
            </el-form-item>
            <el-form-item label="">
              <el-upload
                class="avatar-uploader"
                action="https://jsonplaceholder.typicode.com/posts/"
                :show-file-list="false"
                :on-success="handleAvatarSuccess"
                :before-upload="beforeAvatarUpload"
              >
                <img v-if="imageUrl" :src="imageUrl" class="avatar" />
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <el-form-item label="">
              <el-input type="textarea" v-model="form.desc"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit"></el-button>
              <el-button></el-button>
            </el-form-item>
          </el-form>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'Process',
  data () {
    return {
      form: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      dialogVisible: false,
      imageUrl: '',
      fo: {
        name: 'Admin',
        js: '',
        uid: '1000',
        sex: 'SEX',
        bir: '2000'
      }
    }
  },
  methods: {
    onSubmit () {
      console.log('submit!');
    },
    handleClose (done) {
      this.$confirm('')
        .then(_ => {
          done();
        })
        .catch(_ => { });
    },
    handleAvatarSuccess (res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload (file) {
      const isJPG = file.type === 'image/jpeg';
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error(' JPG !');
      }
      if (!isLt2M) {
        this.$message.error(' 2MB!');
      }
      return isJPG && isLt2M;
    }
  }
}
</script>

<style scoped>
.box {
  margin-left: 20px;
}
.box2 {
  margin: 5%;
}

.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>


